/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/

/* Posts and pages */

.sticky {
	display: block;
}

.hentry {
	margin: 0 0 $base__space-unit;
}

.updated:not(.published){
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.page-content,
.entry-content,
.entry-summary {
	margin: $base__space-unit 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 $base__space-unit;
}

//front-page

.fp-term-link span {
	color: black;
}

/* Asides */

.blog .format-aside .entry-title,
.archive .format-aside .entry-title {
	display: none;
}

/* Comments */

.comment-content a {
	word-wrap: break-word;
}
.bypostauthor {
	display: block;
}

html {
	width: auto;
	// height: 100%;
}

body {
	min-height: 100%;
}

// Front-page configuration *** Start


.page-id-125 {

	.site-header {
		position: absolute;
		width: 100%;
		padding: 20px;
		top: 0;
		left: 0;
	
	}
	.front-page-container {

		display:flex;
		flex-direction: column;
	}

	#primary {
		padding-right: 0px;
		width: 100%;
	}

	.entry-title {
		visibility: hidden;
	}
	.front-banner {
		width: 100%;
		height: 100vh;
		background: 
		url(../../images/logos/inhabitent-logo-full.svg)no-repeat center top 50%,
		linear-gradient(to bottom, rgba(0,0,0,0.3) 5%,  rgba(0,0,0,0.3) 100%),
		url(../../images/home-hero.jpg)no-repeat top;
		background-size: 40vh, cover, cover;
	}

	// .content-area {
	// 	flex-grow: 1;
	// }

	h2 {
		text-align: center;
		color: #2f1339;
		font-size: 3rem;
		margin: 3rem;
		font-family: Novecento Sans Web,sans-serif;
	}

	.front-page-shop { 
		// justify-content: center;
		margin-left: auto;
		margin-right: auto;
		width: 1120px;
		padding-bottom: 40px;
		display:flex;

	}

	.front-page-shop-item {
		display: grid;
		grid-template-rows: auto;
		margin-bottom:0px;
		border: 1px solid #e1e1e1;
		border-top-width: 8px;
		grid-gap: 10px;
		padding: 10px;
		justify-content: space-between;
		text-align: center;
		align-items: center;
		flex:1 1 auto;
		margin:5px;
		
	}

	.fp-term-img {
		width:80px;
		height:80px;
		margin: auto;
	}


	.front-page-journal { 
		justify-content: center;

	}

	.fp-term-link {
		background-color: #248a83;
		color: white;
		font-family: Novecento Sans Web,sans-serif;
		font-size: 15px;
		line-height: 1.5;
		padding: .5rem;
		text-decoration: none;
		text-transform: uppercase;
		border-radius: 1;
		margin:auto;
		width:150px;

	}

	.fp-term-link:hover {
		background: #19625d;
		color:white;
	}

	.fp-term-link span {
		color:white;
	}




}


// Front-page configuration *** End

// Footer configuration *** Start

footer {

	width: 100%;
	background: url(../../images/dark-wood.png);
	// background-size: 512px 512px;
	height: 40vh;
	color: #fff;
}


.footer-content ul {
	list-style-type: none;
}

.social-media ul span {
	display:none;
}

.business-hours ul span {
	font-weight: bold;
}
.contact-info {
	grid-area: contact;
	text-align: left;
	padding-top: 50px;
}

.contact-info ul {
	text-align: left;
	margin: 0;
	padding: 0;
}

.contact-info li {
	margin-top: 20px;
}

.social-media ul {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
}

.social-media li {
	padding: 5px;
	margin-top: 20px;

}

.business-hours {
	grid-area: hours;
	padding-top: 50px;
}

.business-hours ul {
	text-align: left;
	margin: 0;
	padding: 0;
}

.business-hours li {
	margin-top: 20px;
}

.copy-right {
	grid-area: copyright;
	grid-row-start: 4;
  grid-row-end: 6;
	text-align: center;
	padding-top: 30px;
}

.copy-right span {
	text-transform: uppercase;
}

.footer-logo {
	grid-area: footer-logo;
	object-fit: cover;
	text-align: center;
	padding-top: 50px;
}


.footer-content {
	display:grid;
	grid-template-columns: repeat(5, 25%, 15%, 15%, 20%, 25%);
	grid-template-rows: auto;
	grid-template-areas:
	"test contact  hours footer-logo footer-logo"
	"copyright copyright copyright copyright copyright";
	margin-bottom:0px;
	padding:0px;
}

// Footer configuration *** End

// Sidebar configuration

// main container
.site-content {
	display: flex;
	line-height: 1.8rem;
	
}

// child of .site-content
.content-area {
	flex-grow: 1;
	width: 75%;
}

// child of .site-content
.widget-area {
	width: 25%;
	justify-content: center;
	align-items: center;
	padding: 42px 0px 0px 30px;
	line-height: 1.8rem;

}

.sidebar-contact li {
	margin-left: 5px;
	line-height: 1.8rem;
}


.widget-area ul {
	padding: 0;
	margin: 0;
	list-style: none;
}

.day-of-week {
	font-weight: bold;
}

.widget a{
	color: #248a83;
    text-decoration: none;
}

.widget a:hover{
	color: black;
    text-decoration: none;
}

.widget {
	display: inline-block;
}

// Sidebar configuration *** End
