// Journal page configuration start

.blog {

		.journal-container{
			margin: auto;
			width: 1120px;
			display: flex;
		
		}

			// child of container
			.site-content {
				display: flex;
				line-height: 1.8rem;
				
			}

			// child of .site-content
			.content-area {
				flex-grow: 1;
				width: 75%;
				border-right: 1px solid #e1e1e1;
				padding: 50px 60px 50px 0;
				width: 73%;
			}

			// child of .site-content
			.widget-area {
				width: 25%;
				justify-content: center;
				align-items: center;
				padding: 42px 0px 0px 30px;
				line-height: 1.8rem;

			}

		.site-header {
			border-bottom: 1px solid #e1e1e1;

		}
		

		.journal-title {
			background-color: #248a83;
			color: #fff;
			font-size: 35px;
			left: 0;
			top: 0;
			padding: 10px;
			position: absolute;
			margin-top: 2rem;
			max-width: 70%;
			display: block;
			line-height: 3rem;
			
		}

		.journal-header {

			// line-height: 2rem;
			position: relative;
		}

		.journal-meta {
			background-color: #248a83;
			bottom: 0;
			right: 0;
			margin-bottom: .7rem;
			color: #fff;
			display: inline-block;
			font-family: Novecento Sans Web,sans-serif;
			padding: .1rem;
			position: absolute;
			text-transform: uppercase;
		}

		p { 
			margin-bottom: 2rem;
		}

		.read-more {
			background-color: transparent;
			color: black;
			border: 1px solid #000;
			font-family: Novecento Sans Web,sans-serif;
			font-size: 15px;
			line-height: 1;
			padding: .75rem 1.5rem;
			text-decoration: none;
			text-transform: uppercase;
			border-radius: 1;
			margin-bottom: 6rem;
			
		}

		.read-more:hover {
			color:white;
			background-color: #248a83;
		}
}

